.poke-trainer{position:fixed;right:0;width:auto;height:auto;top:139px;z-index:-1}.pokemon-wrapper{padding:2.5rem 1.5rem}.pokemon-wrapper__container{padding:24px}.pokemon-wrapper__header{display:inline-flex;padding:1.5rem}.pokemon-wrapper__list{position:relative;display:grid;grid-template-columns:repeat(1, 1fr);gap:2rem 1rem;grid-auto-rows:minmax(300px, auto);flex-wrap:wrap;padding:0;margin:0;list-style:none;width:300px;z-index:10}.pokemon-wrapper__list-item{position:relative;display:flex;flex-direction:column;justify-content:center;text-align:center;margin:0;padding:16px 24px;border-radius:20px;backdrop-filter:blur(7px);background:linear-gradient(154deg, rgba(121, 117, 131, 0.02) 5.35%, rgba(54, 53, 103, 0.02) 83.85%),rgba(0,0,0,.3);z-index:2}.pokemon-wrapper__list-item img{transform:scale(1);transition:transform .3s ease-out;background:#fff;border-radius:50%;padding:12px;object-fit:scale-down;height:96px;width:96px;box-shadow:0 4px 4px 0 rgba(0,0,0,.39) inset}.pokemon-wrapper__list-item img:hover{transform:scale(1.15);cursor:pointer}.pokemon-wrapper__content{display:flex;flex-direction:column;align-items:center;justify-content:center;gap:1rem;position:relative;z-index:10}.pokemon-wrapper__list-header{display:flex;justify-content:center;text-transform:uppercase}.pokemon-wrapper__toggle-wrapper{width:100%;display:flex;justify-content:space-between;position:fixed;bottom:0;left:0;background:#000;z-index:10;opacity:.9;padding:24px}.pokemon-wrapper__toggle{padding:1rem;background:rgba(0,0,0,0);border:1px solid #fff;color:#fff;cursor:pointer}